import React from 'react';
import { Link } from 'react-router-dom';
import { Container, Col, Row, Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle, Button } from 'reactstrap';


const PokemonDetail = ({ pokemon }) => {
    const { id, name, sprite, type, data} = pokemon;
    return (
        <Container>
        <Row>
        <Col sm="6">
            <Card>
                <CardImg top width="100%" src={sprite} alt={name} />
                <CardBody>
                <CardTitle>{name} | {id}</CardTitle>
                <CardSubtitle>Tipo: </CardSubtitle>
                    <ul>
                    {
                        data && data.types.map((type, index) => (
                            <li key={index}>{ type.type.name }</li>
                        ))
                    
                    }
                    </ul>
                    <Link to="/"><Button color="primary">Voltar</Button></Link>
                </CardBody>
            </Card>
        </Col>   
        </Row> 
        </Container>
       
    )
  }
  
  export default PokemonDetail;
  



